.cartItem {
    display: flex;
    align-items: center;
    width: 880px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 0.0625em 0.0625em, rgba(0, 0, 0, 0.25) 0px 0.125em 0.5em, rgba(255, 255, 255, 0.1) 0px 0px 0px 1px inset;
    margin-top: 30px;
    position: relative;

    .quantity {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }

    img {
        width: 100px;
        height: 100px;
        margin-right: 70px;
    }

    h3 {
        font-size: 21px;
        margin-right: 50px;
        width: 90px;
    }

    p {
        margin-right: 100px;
        font-size: 19px;
        width: 90px;
        text-align: center;
    }

    h2 {
        margin-left: 90px;
        font-size: 20px;
        margin-right: 20px;
        width: 120px;
        text-align: center;
    }

    span {
        font-size: 21px;
        margin: 0 5px;
    }

    button {
        width: 25px;
        height: 25px;
        padding: 0 5px;

        &:hover {
            cursor: pointer;
        }
    }

    .remove_item {
        color: red;
        width: 40px;
        height: 40px;
        margin-bottom: 60px;
        margin-left: 70px;
        transition: 0.2s;

        &:hover {
            cursor: pointer;
            transform: rotate(360deg);
        }
    }
}


@media only screen and (max-width:600px) {
    .cartItem .remove_item {
        position: relative;
        bottom: 220px;
        left: 70px;
        margin-bottom: 0px;
    }
}