.profile_container {
    display: flex;
    justify-content: center;
    margin-bottom: 400px;

    .profile_content {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 5px 60px;
        box-shadow: rgba(0, 0, 0, 0.19) 0px 10px 20px, rgba(0, 0, 0, 0.23) 0px 6px 6px;
        margin-top: 50px;

        .profile_icon {
            font-size: 120px;
            margin: 0;
        }

        p {
            font-size: 20px;
            background-color: lightgreen;
            color: #000;
            padding: 6px;
            border-radius: 3px;
            margin-top: 40px;
            margin-bottom: 10px;
        }

        .fav_items {
            position: relative;
            text-decoration: none;
            font-size: 20px;
            background-color: rgb(16, 129, 204);
            color: #fff;
            padding: 6px;
            border-radius: 3px;
            margin-bottom: 40px;
            transition: transform 0.2s ease-out;

            &:hover {
                transform: scale(1.1);
            }
        }
    }
}