//SASS placeholer for input and textarea fields
%input-textarea-fields {
    font-size: 21px;
    padding: 10px;
    outline: none;
    margin-top: 20px;
    width: 400px;
    background-color: #f5f5f5;
}

//styles
.contacts_container {
    display: flex;
    justify-content: center;

    form {
        display: flex;
        flex-direction: column;
        margin: 50px 100px;

        h2 {
            font-size: 31px;
        }

        input {
            @extend %input-textarea-fields;
        }

        textarea {
            @extend %input-textarea-fields;
        }

        button {
            font-size: 22px;
            padding: 8px;
            border-radius: 5px rgb(16, 129, 204);
            outline: none;
            background-color: rgb(16, 129, 204);
            transition: 0.2s ease-in;
            margin-top: 20px;
            margin-bottom: 20px;
            width: 400px;

            &:hover {
                background-color: rgb(8, 86, 138);
                cursor: pointer;
            }
        }
    }

    .errorMsg {
        color: red;
        font-size: 20px;
        margin-top: 10px;
    }
}


.feedback_msg {
    display: flex;
    align-items: center;
    justify-content: center;

    h3 {
        font-size: 29px;
        margin-top: 15px;
        color: rgb(44, 44, 204);
    }
}


@media only screen and (max-width:600px) {
    .contacts_container {
        flex-direction: column;
        align-items: center;
        justify-content: center;

        form {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            margin-bottom: -50px;
            margin-right: 100px;

            input,
            textarea,
            button {
                width: 300px;
                font-size: 17px
            }
        }
    }
}